
.my-prize {
  background: #FFFBFD;
}
.p-title {
  width: 207px;
  height: 19px;
  display: block;
  margin: auto;
  background: url("../my-prize/images/title@2x.png") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  margin-top: 27px;
  font-family: '.PingFangSC-Semibold';
  font-size: 20px;
  color: #FE4744;
}
.my-tb {
  height: 16.2vw;
  line-height: 16.2vw;
  table-layout: fixed;
  width: 92.2vw;
  margin: auto;
  margin-top: 4vw;
  border-radius: 1.9vw;
  border: 2px solid #FE4744;
  border-right: none;
  border-left: none;
  background: #FFFFFF;
  padding-left: 4vw;
  padding-right: 4vw;
  color: #FE4744;
  font-size: 3rem;
  box-sizing: border-box;
}
.my-tb li {
    width: 50%;
    float: left;
    text-align: left;
}
.my-tb li:last-child {
      text-align: right;
}
.my-tb .btn {
    border: 1px solid #FE4744;
    background: none;
    border-radius: 2vw;
    font-family: '.PingFangSC-Regular';
    font-size: 3rem;
    color: #FE4744;
    letter-spacing: -0.91px;
}
.my-p {
  height: 36.8vw;
  table-layout: fixed;
  width: 92.2vw;
  margin: auto;
  margin-top: 4vw;
  border-radius: 1.9vw;
  border: 2px solid #FE4744;
  border-right: none;
  border-left: none;
  background: #FFFFFF;
  padding-left: 10vw;
  padding-right: 10vw;
  color: #FE4744;
  font-size: 3rem;
  box-sizing: border-box;
}
.my-p li {
    width: 50%;
    float: left;
    text-align: center;
}
.my-p li .p-pic {
      display: inline-block;
      height: 19.7vw;
      width: 20vw;
      margin-top: 5.2vw;
      border: 1px solid #C7C7C7;
      margin-bottom: 2vw;
}
.p-pic p {
  text-align: center;
}



