
html{
    font-size: 1vw;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   width: 100vw;
  padding-bottom: 20vh;
}
.p-title{
    width:207px;
    height:19px;
    display: block;
    margin: auto;
    background: url("./components/my-prize/images/title@2x.png")no-repeat;
    background-size: 100% 100%;
    text-align: center;
    margin-top: 27px;
    font-family: '.PingFangSC-Semibold';
    font-size:20px;
    color: #FE4744;
}
.sort-more{
    font-family: '.PingFangSC-Regular';
    font-size: 3rem;
    color: #FE4744;
    letter-spacing: 0;
    position: absolute;
    right: 3vw;
    top: 1.3vw;
}



