
@charset "UTF-8";
.my-prize-five {
  position: relative;
}
.coupon {
  height: 22.66vw;
  width: 91.73vw;
  margin: auto;
  margin-top: 4vw;
  position: relative;
}
.get {
  background: url("../my-prize/images/优惠券@2x.png") no-repeat;
  background-size: 100% 100%;
}
.gone {
  background: url("../my-prize/images/优惠券已过期@2x.png") no-repeat;
  background-size: 100% 100%;
}
.already {
  background: url("../my-prize/images/优惠券已使用@2x.png") no-repeat;
  background-size: 100% 100%;
}
.money {
  display: inline-block;
  height: 22.66vw;
  width: 25vw;
  line-height: 22.66vw;
  font-family: 'LucidaGrande';
  vertical-align: top;
  font-size: 5rem;
  color: #FFFFFF;
  text-align: center;
  letter-spacing: -0.83px;
}
.money span {
    font-family: '.PingFangSC-Regular';
    font-size: 13rem;
    color: #FFFFFF;
    letter-spacing: -2px;
}
.m-text {
  font-family: 'PingFangSC-Regular';
  font-size: 3rem;
  letter-spacing: -1.02px;
  display: inline-block;
  padding-left: 4vw;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 15.2vw;
}
.m-text p {
    margin-top: 1.4vw;
    white-space: nowrap;
    font-family: 'PingFangSC-Regular';
    font-size: 2.6rem;
    color: gray;
    letter-spacing: -1.02px;
}
.use {
  height: 6.1vw;
  line-height: 5vw;
  font-family: PingFangSC-Regular;
  font-size: 3rem;
  color: #D11414;
  letter-spacing: 0.33px;
  border: 1px solid #D11414;
  background: none;
  border-radius: 3vw;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 3vw;
  box-sizing: border-box;
}



